<template>
  <div class="my" v-if="loginUser">
    <div class="personal">
      <div class="personalson">
        <div class="headportrait">
          <img :src="loginUser.avatar" alt="" />
        </div>
        <div class="text">
          <p>
            <span>{{ loginUser.nickname }}</span>
            <span
              ><img :src="loginUser.vip_icon" alt="" />
              {{ loginUser.vip_name }}</span
            >
          </p>
          <p>
            ID:&nbsp;{{ loginUser.uid }} &nbsp;<van-icon
              class="icones"
              name="edit"
            />
          </p>
        </div>
      </div>
      <router-link to="/dataert" class="setup" tag="div">
        <van-icon name="setting-o" />
      </router-link>
    </div>
    <div class="personal1"></div>
    <div class="wrapper">
      <van-grid class="wrapperson">
        <van-grid-item class="wrappersonitem">
          <template #icon>
            <div>我的余额</div>
          </template>
          <template #text>
            <div class="textson">{{ loginUser.now_money }}</div>
          </template>
        </van-grid-item>
        <van-grid-item class="wrappersonitem">
          <template #icon>
            <div>当前佣金</div>
          </template>
          <template #text>
            <div class="textson">{{ loginUser.commissionCount }}</div>
          </template>
        </van-grid-item>
        <van-grid-item class="wrappersonitem">
          <template #icon>
            <div>优惠卷</div>
          </template>
          <template #text>
            <div class="textson">{{ loginUser.couponCount }}</div>
          </template>
        </van-grid-item>
      </van-grid>
      <van-cell-group class="titlert">
        <van-cell center is-link>
          <template #title>
            <div>
              <span class="custom-title titleson">我的订单</span>
            </div>
          </template>
          <template #default>
            <div class="titleson3">
              <span class="more">全部订单</span>
            </div>
          </template>
        </van-cell>
      </van-cell-group>
      <div class="orderState" v-if="shopshang">
        <van-grid :column-num="5">
          <van-grid-item @click="Jump(0)">
            <img src="../assets/3.png" alt="" />
            <span v-if="shopshang.unpaid_count != 0">{{
              shopshang.unpaid_count
            }}</span>
            <p>待付款</p>
          </van-grid-item>
          <van-grid-item @click="Jump(1)">
            <img src="../assets/4.png" alt="" />
            <span v-if="shopshang.order_count != 0">
              {{ shopshang.order_count }}
            </span>
            <p>待发货</p>
          </van-grid-item>
          <van-grid-item @click="Jump(2)">
            <img src="../assets/5.png" alt="" />
            <span v-if="shopshang.received_count != 0">
              {{ shopshang.received_count }}
            </span>
            <p>待收货</p>
          </van-grid-item>
          <van-grid-item @click="Jump(3)">
            <img src="../assets/6.png" alt="" />
            <span v-if="shopshang.evaluated_count != 0">
              {{ shopshang.evaluated_count }}
            </span>
            <p>待评价</p>
          </van-grid-item>
          <van-grid-item @click="Jump(4)">
            <img src="../assets/7.png" alt="" />
            <span v-if="shopshang.complete_count != 0">
              {{ shopshang.complete_count }}
            </span>
            <p>售后/退款</p>
          </van-grid-item>
        </van-grid>
      </div>
      <div class="serviceList">
        <div class="service">我的服务</div>
        <van-grid :column-num="4">
          <van-grid-item
            v-for="item in userxin"
            :key="item.id"
            :icon="item.pic"
            @click="hoprouting(item.wap_url)"
            :text="item.name"
          />
        </van-grid>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapMutations } from "vuex";
export default {
  data() {
    return {
      userxin: [],
      loginUser: null,
      shopshang: null,
    };
  },
  created() {
    console.log(this.$store.getters.loginUser);
    this.$store.getters.loginUser;
    this.loginUser = this.$store.getters.loginUser.data;
    this.$axios.get("/api/menu/user").then((d) => {
      this.userxin = d.data.data.routine_my_menus;
    });
    this.$axios.get("/api/user").then((d) => {
      this.shopshang = d.data.data.orderStatusNum;
    });
  },
  methods: {
    hoprouting(event) {
      if (event == "/user/add_manage") {
        this.$router.push({ path: "/user/add_manage" });
      } else {
        this.$toast({
          message: "功能维护中",
          icon: "info-o"
        });
        return;
      }
    },
    //跳转我的订单列表
    Jump(event) {
      console.log(event);
      this.$router.push({ name: "Order", params: { type: event } });
    },
    logOut() {
      this.loginOut();
      this.$toast({
        message: "退出成功!",
        onClose: () => {
          this.$router.push("/login");
        },
      });
    },
    ...mapMutations(["saveToken", "saveLoginInfo", "loginOut"]),
  },
  computed: {
    ...mapGetters(["loginUser"]),
  },
};
</script>
<style lang="less">
.my {
   min-height: 100vh;
  background-color: rgb(245, 245, 245);
  .personal {
    background-color: #e93323;
    height: 94px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    .personalson {
      height: 59px;
      display: flex;
      align-items: center;
      .headportrait {
        img {
          width: 59px;
          height: 59px;
          border-radius: 50%;
        }
      }
      .text {
        margin-left: 17.5px;
        font-size: 16px;
        height: 38px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        p {
          margin: 0;
          &:first-child {
            color: #fff;
            display: flex;
            align-items: center;
            span {
              &:nth-child(2) {
                font-size: 12px;
                height: 17.8px;
                border-radius: 8.9px;
                background-color: rgba(0, 0, 0, 0.2);
                margin-left: 8.3px;
                display: flex;
                align-items: center;
                padding: 0 5px;
                img {
                  width: 14px;
                  margin-right: 5px;
                }
              }
            }
          }
          &:nth-child(2) {
            font-size: 13px;
            color: hsla(0, 0%, 100%, 0.6);
            .icones {
              font-size: 15px;
            }
          }
        }
      }
    }
    .setup {
      color: #fff;
      font-size: 18px;
    }
  }
  .personal1 {
    width: 100%;
    height: 50px;
    position: absolute;
    border-radius: 0 0 50% 50%;
    background-color: #e93323;
  }
  .wrapper {
    padding: 0 10px;
    .wrapperson {
      background-color: #fff;
      border-radius: 5px;
      font-size: 13px;
      width: 100%;
      display: flex;
      .wrappersonitem {
        flex: 1;
        color: #aaa;
        .van-grid-item__content{
           background-color: transparent;
          .textson {
            margin-top: 5px;
            font-size: 17.8px;
            color: #282828;
          }
        }
      }
    }
    .titlert {
      background-color: white;
      margin-top: 7.5px;
      .van-cell-group {
        background-color: transparent;
      }
      .van-cell {
        background-color: transparent;
      }
      .van-cell__right-icon {
        color: #282828;
      }
      .van-cell__title {
        -webkit-flex: 3;
        .titleson {
          color: #282828;
          font-size: 15px;
          margin-right: 10px;
        }
        .titleson2 {
          color: #666;
          font-size: 12px;
        }
      }
      .van-cell__value {
        width: 76px;
        .titleson3 {
          color: #666;
          width: 76px;
          font-size: 13px;
          span {
            width: 76px;
            color: #666;
          }
        }
      }
    }
    .orderState {
      .van-grid-item__content {
        padding: 0;
        position: relative;
        span {
          background-color: #fff;
          color: #ee5a52;
          border-radius: 12px;
          position: absolute;
          right: 15px;
          top: 7px;
          font-size: 12px;
          padding: 0 4px;
          border: 1px solid #ee5a52;
        }
      }
      img {
        margin-top: 16.5px;
        width: 24.5px;
        height: 21px;
      }
      p {
        font-size: 13px;
        margin-top: 9px;
        color: #454545;
      }
    }
    .serviceList{
      .van-grid{
        .van-grid-item{
          .van-grid-item__content {
            padding: 7.5px 8px;
          }
        }
      }
      .service {
        background-color: #fff;
        margin-top: 7.5px;
        padding: 0 15px;
        line-height: 45px;
        font-size: 15px;
      }
    }
  }
}
</style>